.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 50px;
  width: 100%;
  text-align: justify;
}

.container1 {
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 50px;
  width: 100%;
  height: 90vh;
  text-align: justify;
  
}

.container2 {
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px;
  width: 100%;
  height: 110vh;
  text-align: justify;
}


/*********************************************************************
Cos del lloc
**********************************************************************/
body {
    color: #1f1f1f;
    font-size: 2vh;
    font-family: Arial, Helvetica, sans-serif;
    background-color: rgb(243, 170, 61);  
  }


  a:link {
    color: rgb(0, 0, 0);  
  }

  a:visited {
    color: rgb(206, 45, 45);
  }

  h1, h2 {

    margin-top: 50px;
    margin-bottom: 40px;
  }

  p{

    margin-top: 40px;
    margin-bottom: 20px;
  }

  .centrar{
    text-align: center;
  }

  .centrar ol, ul{
    text-align: left;
    display: inline-block;
  }


  /***********************************************************************
  Header
  ***********************************************************************/
  header {
    background-color:white;
    margin-right: auto;
    margin-left: auto;
    width: 100%;
    box-shadow: 0px 10px black ;
  }

  nav ul li{
    display: inline-block;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 1.5rem;
  
  }

  .circle {
  margin-left: 45%;
  animation: 2s infinite alternate circle;
  }

  @keyframes circle {
    from {
      -webkit-clip-path: circle(0%);
      clip-path: circle(0%);
    }
    to {
      -webkit-clip-path: circle(120px);
      clip-path: circle(120px);
    }
  }

  nav {
    text-align: center;
  }
 
  @media (max-width: 540px) {
    #log-centrar{
      margin-left: 30%;
      margin-right: 25%;
    }
  }
  
/***************************************************************************
Main
****************************************************************************/

main{
  box-shadow: 0px 30px 30px 30px black;
}
  
/***************************************************************************
Footer
***************************************************************************/
  footer{
    background-color: rgb(0, 0, 0);
    color: rgb(255, 255, 255);
    height: 15vh;
    margin-left: auto;
    margin-top: auto;
    font-size: 1rem; 

  }
  
  footer a:link {
    color: rgb(255, 255, 255);  
  }

  .pfooter{
  font-size: 0.8rem; 
  padding-top: 1px; 
  text-align: center;
  
  }

  .logo2{
    margin-left: 50px;
    margin-top: 20px;

  }

  .footer{
    margin-top: -120px;

  }

  @media (max-width: 540px) {
    #centrar {
      text-align: center;
   
      }
    #font{
      font-size: 1rem;
      display: list-item;
      list-style-type: none;
    }
    #copyright{
      font-size: 0.8rem;
      margin-left: 30px;
      margin-right: 30px;
    }
    #centrar {
      text-align: center;
      }
    #logofooter{
      opacity: 40%;
      margin-left: 5%;
      }
    
    #footer{
      height: 25vh;
    }
    }


  
  /***********************************************************************
  Pantalla d'inici
  ************************************************************************/
  .home {
    background-image: url(../images/rellotge.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center top;  
  }

  body.home nav ul li {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.5rem;
    display: inline-block;
    padding-left: 40px;
    padding-top: 4vh;

  }

  #indexnav a:link{
    color:rgb(255, 255, 255);
  } 

  .titol{
    color: blanchedalmond;
    font-size: 4rem;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    padding-top: 25%;
    -webkit-text-stroke: 2px rgb(11, 49, 129);

  }

  @media (max-width: 540px) {
   #titolresponsive{
     font-size: 3rem;
     -webkit-text-stroke: 1px rgb(11, 49, 129);
   }
}
  .subtitol{
    color: blanchedalmond;
    font-size: 2.1rem;
    font-family: Arial, Helvetica, sans-serif;
    -webkit-text-stroke: 1px rgb(11, 49, 129);
  }

  @media (max-width: 540px) {
    #subtitolresponsive{
      font-size: 2rem;
      -webkit-text-stroke: 1px rgb(11, 49, 129);
    }
    #indexnav{
      font-size: 1.1rem;
    }
    #navindex{
      margin-right: 30px;
    }
 }
  .sncolor{
    background-color:transparent;
    box-shadow: none;
  }

  #unshadow{
    box-shadow: none;
  }


