  /************************************************************************
  Pàgina de detall
  ************************************************************************/
  
  
  .flex-container{
    display: flex;
    flex-direction: row;
  }

  figcaption {
    text-align: center;
    font-size: 1rem;
  }

  figure {
    font-style: italic;

  }
  
  .img-fluid{

    max-width: 100%;

  }

  .li{
    font-size: 1.3rem;
  }

  @media (max-width: 540px) {
    #enllaç {
      font-size: 0.7rem;
      }
      #navenllaç{
        text-align: center;
      }
      #titoladaptat{
        font-size: 1.8rem;
      }
    }

  aside{
    text-align: center;
    margin-top: 20px;
  }

 

  /********************************************************************************
  Pàgina presentació
  ********************************************************************************/
  .formats{
    display: flex;
    flex-direction: row;
    gap: 50px;
    margin-top: 20px;
  }

  @media (max-width: 540px) {
      #titoladaptat2{
        font-size: 1.7rem;
      }
    }

  .cerle{
      animation: col 3s linear infinite;
    }

   @keyframes col {

    0%,71% {fill:inherit}
    72% {fill:rgb(255, 0, 0)}
    75%,100% {fill:currentColor}
   }

  /******************************************************************************
  Pàgina categoria
  *****************************************************************************/
  ol{
    padding-top: 10px;
    padding-bottom: 30px;
  }

  .just{
    padding-left: 40px;
  }

  .epoc{

    padding-top: 10px;
    padding-bottom: 10px;
  }

  .destacada{
    width: 27%;
    height: 7vh;
    margin-top: 15px;
    margin-left: 100px;
  }

  #amaga{
    display: none;
  }

  .contenidor:hover>#amaga{
    display: inline;
  }

  .contenidor:hover .destacada {-webkit-transform:scale(1.5);transform:scale(1.5);}
  .contenidor {overflow:hidden;}